.intro {
    margin-top: 2.8rem;
    width: 100%;
    height: 25rem;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    background: #fafafa;
    -ms-flex-pack: center;
        justify-content: center;
}
.textCover {
    margin: 0 5rem;
}
.ityped-cursor {
	font-size: 1.2rem;
    opacity: 1;
    -webkit-animation: blink 0.5s infinite;
    animation: blink 0.5s infinite;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
}
.aniText{
	font-size: 1.3rem;
    width: 1000px;
}
@keyframes blink {
    100% {
        opacity: 0;
    }
}
@-webkit-keyframes blink {
    100% {
        opacity: 0;
    }
}



